import React from 'react'
import { useSelector } from 'react-redux'
import { selectedAnimes } from '../../store/selectors'
import Card from '../card/Card'
import s from './cards.module.css'

function Cards({animes, isDeleteOption}) {
    
    

    return (
        <ul className={s.main}>
            {
                animes.map(anime => <Card 
                    key={anime.id} 
                    name={anime.name} 
                    photo={`https://busy-erin-lion-suit.cyclic.app/${anime.images?.card}`}
                    rating={anime.rating}
                    restriction={anime.restriction}
                    url={`/main${anime.url}`}
                    isDeleteOption={isDeleteOption}
                    id={anime.id}
                />)
            }
        </ul>
    )
}

export default React.memo(Cards)